
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DesignBarcodeComponent } from './design-barcode.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core'; 
import { IonicModule } from '@ionic/angular';
const routes: Routes = [
  {
    path: '',
    component: DesignBarcodeComponent
  }, 
  {
    path: 'label-products',
    loadChildren: () => import('./label-products/label-products.module').then(m => m.LabelProductsModule)
  },
  {
    path: 'label-lots',
    loadChildren: () => import('./label-lots/label-lots.module').then(m => m.LabelLotsModule)
  },
]


@NgModule({
  declarations: [
    DesignBarcodeComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule, 
    IonicModule
  ]
})
export class DesignBarcodeModule { }
